<template>
  <section id="introduction">
    <v-img
      :max-height="maxHeight"
      :src="require('@/assets/office.png')"
      class="white--text"
      gradient="to right, rgba(5, 11, 31, .6), rgba(5, 11, 31, .2)"
    >
      <v-container class="fill-height px-4">
        <v-responsive
          class="d-flex align-center"
          height="100%"
          max-width="700"
          width="100%"
        >
          <base-heading title="company.name" />

          <base-body html>
            <span class="font-weight-bold">{{ $t("company.desc") }}</span>
            <br/>
            <span class="font-weight-bold">{{ $t("company.history") }}</span>
            <a
              v-for="[name, href] in services"
              :key="name"
              :href="href"
              class="pa-1 white--text body-1 d-block transparent"
              height="auto"
              color="white"
              v-html="`&rsaquo; ${name}`"
            />
          </base-body>
        </v-responsive>
      </v-container>
    </v-img>
  </section>
</template>

<script>
  export default {
    name: 'SectionIntroduction',

    provide: {
      theme: { isDark: true },
    },

    computed: {
      maxHeight () {
        const height = this.$vuetify.breakpoint.mdAndUp ? '100vh' : '50vh'

        return `calc(${height} - ${this.$vuetify.application.top}px)`
      },

      services() {
        return [
          [this.$t('services.wechat.desc'), '#product-software'],
          [this.$t('services.android.desc'), '#'],
          [this.$t('services.iot.desc'), '#product-thermal-imager']
        ]
      }
    },
  }
</script>

<style lang="sass">
  a
    text-decoration: none
</style>